.App {
    font-family: sans-serif;
    text-align: center;
    width: 100%;
    height: 100%;
  }
  .excalidraw-wrapper {
    height: calc(100vh - 20px) !important;
    margin: 10px !important;
  }
  .button-wrapper button {
    z-index: 1;
    height: 40px;
    max-width: 200px;
    margin: 10px;
    padding: 5px;
  }
  
  .excalidraw .App-menu_top .buttonList {
    display: flex;
  }
  
  .excalidraw-wrapper {
    height: 800px;
    margin: 50px;
  }
  
  :root[dir="ltr"] .excalidraw .layer-ui__wrapper .zen-mode-transition.App-menu_bottom--transition-left {
    transform: none
  }
  
  .excalidraw .panelColumn {
    text-align: left;
  }
  
  .export-wrapper {
    display: flex;
    flex-direction: column;
    margin: 50px;
  }
  .export-wrapper__checkbox {
    display: flex;
  }
.excalidraw_top-right_wrap {
  height: 24px;
}
.excalidraw_top-right_select {
  height: 100%;
  margin-right: 8px;
}
.excalidraw_top-right_button {
  height: 100%;
}
.excalidraw_top-right_function {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
  background-color: var(--island-bg-color);
  box-shadow: var(--shadow-island);
  border-radius: 4px;
  padding: 8px;
  transition: box-shadow 0.5s ease-in-out;
}
.excalidraw_top-right_function > div {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: var(--space-factor);
  background-color: var(--button-gray-1);
}